import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isPlaying: false,
    currentTime: 0,
    score: 0,
    combo: 0,
    rhythmNotes: [] // 预生成的节奏点数据
  },
  getters: {
  },
  mutations: {
    SET_PLAYING (state, val) { state.isPlaying = val },
    UPDATE_TIME (state, time) { state.currentTime = time },
    ADD_SCORE (state, points) {
      state.score += points
      state.combo += 1
    },
    RESET_COMBO (state) { state.combo = 0 }
  },
  actions: {

  },
  modules: {
  }
})
