<template>
    <div class="map-container">
      <v-chart
        :options="mapOptions"
        :autoresize="true"
        @click="handleMapClick"
        style="width: 100%; height: 100vh;"
      />
    </div>
  </template>

<script>
export default {
  data () {
    return {
      mapOptions: {
        title: {
          text: '中国地图',
          left: 'center'
        },
        tooltip: {
          trigger: 'item',
          formatter: '{b}'
        },
        series: [{
          name: '中国',
          type: 'map',
          mapType: 'china',
          roam: false,
          label: {
            show: true,
            color: '#333'
          },
          itemStyle: {
            areaColor: '#e7f0fd',
            borderColor: '#398fde'
          },
          emphasis: {
            label: {
              color: '#fff'
            },
            itemStyle: {
              areaColor: '#398fde'
            }
          }
        }]
      }
    }
  },
  methods: {
    handleMapClick (params) {
      if (params.name) {
        this.$router.push({
          path: `/province/${params.name}`
        })
      }
    }
  }
}
</script>

  <style scoped>
  .map-container {
    width: 100%;
    height: 100vh;
  }
  </style>
