<template>
    <div class="assignment-container">
      <div class="header">
        <h1>🎵 音频作业提交系统</h1>
        <p>上传你的音频作品，获取即时评分</p>
      </div>

      <!-- 文件上传区域 -->
      <div
        class="upload-section"
        @dragover.prevent="dragover = true"
        @dragleave="dragover = false"
        @drop.prevent="handleDrop"
        :class="{ 'drag-active': dragover }"
      >
        <div class="upload-wrapper">
          <div class="icon-box">
            <i class="fas fa-cloud-upload-alt"></i>
          </div>
          <input
            type="file"
            id="audioInput"
            accept="audio/*"
            @change="handleFileChange"
            :disabled="isUploading"
          >
          <label for="audioInput" class="file-label">
            {{ fileLabelText }}
          </label>
          <div class="hint-text">支持格式：MP3, WAV, OGG</div>
        </div>

        <button
          class="submit-btn"
          @click="handleUpload"
          :disabled="!audioFile || isUploading"
        >
          <span v-if="!isUploading">🚀 提交作业</span>
          <div v-else class="loading">
            <div class="dot-flashing"></div>
          </div>
        </button>
      </div>

      <!-- 上传结果展示 -->
      <transition name="score-fade">
        <div v-if="score !== null" class="result-section">
          <div class="result-card">
            <div class="file-info">
              <i class="fas fa-file-audio"></i>
              <span>{{ audioFile.name }}</span>
            </div>
            <div class="score-display">
              <div class="score-label">综合评分</div>
              <div class="score-value">{{ score }}</div>
              <div class="score-bar">
                <div
                  class="progress"
                  :style="{ width: score + '%' }"
                ></div>
              </div>
              <div class="score-tips">
                {{ getScoreTips(score) }}
              </div>
            </div>
          </div>
        </div>
      </transition>
    </div>
  </template>

<script>
export default {
  data () {
    return {
      isUploading: false,
      score: null,
      audioFile: null,
      dragover: false
    }
  },
  computed: {
    fileLabelText () {
      if (this.audioFile) return this.audioFile.name
      return '点击选择或拖拽音频文件到这里'
    }
  },
  methods: {
    handleDrop (e) {
      this.dragover = false
      const file = e.dataTransfer.files[0]
      this.validateAndSetFile(file)
    },

    handleFileChange (e) {
      const file = e.target.files[0]
      this.validateAndSetFile(file)
    },

    validateAndSetFile (file) {
      if (file && file.type.startsWith('audio/')) {
        this.audioFile = file
      } else {
        this.showError('请选择有效的音频文件')
        this.audioFile = null
      }
    },

    async handleUpload () {
      try {
        this.isUploading = true
        // 模拟上传延迟
        await new Promise(resolve => setTimeout(resolve, 2000))
        this.generateRandomScore()
      } finally {
        this.isUploading = false
      }
    },

    generateRandomScore () {
      const min = 60
      const max = 100
      this.score = Math.floor(Math.random() * (max - min + 1)) + min
    },

    getScoreTips (score) {
      if (score >= 90) return '完美表现！继续保持！🎉'
      if (score >= 80) return '非常不错，还有提升空间！👍'
      if (score >= 70) return '良好，继续努力！💪'
      return '加油，下次会更好！✨'
    },

    showError (msg) {
      alert(msg)
    }
  }
}
</script>

  <style scoped>
  @import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.4/css/all.min.css');

  :root {
    --primary: #6366f1;
    --primary-hover: #4f46e5;
    --success: #10b981;
    --text: #374151;
    --bg: #f8fafc;
  }

  * {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }

  body {
    font-family: 'Segoe UI', system-ui, sans-serif;
    line-height: 1.5;
    color: var(--text);
    background: var(--bg);
  }

  .assignment-container {
    max-width: 800px;
    margin: 2rem auto;
    padding: 2rem;
  }

  .header {
    text-align: center;
    margin-bottom: 3rem;
  }

  .header h1 {
    font-size: 2.5rem;
    color: var(--primary);
    margin-bottom: 0.5rem;
  }

  .header p {
    color: #6b7280;
    font-size: 1.1rem;
  }

  /* 上传区域 */
  .upload-section {
    background: white;
    border-radius: 1rem;
    padding: 2rem;
    box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
    transition: 0.3s;
    border: 2px dashed transparent;
  }

  .upload-section.drag-active {
    border-color: var(--primary);
    background: #f0f9ff;
  }

  .upload-wrapper {
    position: relative;
    text-align: center;
    margin-bottom: 2rem;
  }

  .icon-box {
    width: 80px;
    height: 80px;
    background: #e0e7ff;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto 1rem;
    font-size: 2rem;
    color: var(--primary);
  }

  input[type="file"] {
    position: absolute;
    left: -9999px;
  }

  .file-label {
    display: block;
    padding: 1rem 2rem;
    background: #f3f4f6;
    border-radius: 0.5rem;
    cursor: pointer;
    transition: 0.2s;
    font-weight: 500;
  }

  .file-label:hover {
    background: #e5e7eb;
  }

  .hint-text {
    color: #6b7280;
    font-size: 0.9rem;
    margin-top: 1rem;
  }

  /* 提交按钮 */
  .submit-btn {
    display: block;
    width: 100%;
    padding: 1rem;
    background: linear-gradient(135deg, var(--primary), var(--primary-hover));
    color: white;
    border: none;
    border-radius: 0.5rem;
    font-size: 1.1rem;
    cursor: pointer;
    transition: 0.3s;
    position: relative;
    overflow: hidden;
  }

  .submit-btn:disabled {
    opacity: 0.7;
    cursor: not-allowed;
  }

  .submit-btn:hover:not(:disabled) {
    transform: translateY(-2px);
    box-shadow: 0 5px 15px rgba(99, 102, 241, 0.3);
  }

  /* 加载动画 */
  .dot-flashing {
    position: relative;
    width: 10px;
    height: 10px;
    border-radius: 5px;
    background-color: white;
    animation: dotFlashing 1s infinite linear;
  }

  @keyframes dotFlashing {
    0% { box-shadow: 15px 0 white, -15px 0 rgba(255,255,255,0.2); }
    50% { box-shadow: 15px 0 rgba(255,255,255,0.2), -15px 0 white; }
    100% { box-shadow: 15px 0 white, -15px 0 rgba(255,255,255,0.2); }
  }

  /* 评分结果 */
  .score-fade-enter-active {
    transition: all 0.5s ease;
  }

  .score-fade-enter-from {
    opacity: 0;
    transform: translateY(20px);
  }

  .result-section {
    margin-top: 2rem;
  }

  .result-card {
    background: white;
    border-radius: 1rem;
    padding: 2rem;
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
  }

  .file-info {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    color: #6b7280;
    margin-bottom: 1.5rem;
  }

  .file-info i {
    font-size: 1.2rem;
  }

  .score-display {
    text-align: center;
  }

  .score-label {
    color: var(--primary);
    font-weight: 600;
    margin-bottom: 1rem;
  }

  .score-value {
    font-size: 4rem;
    font-weight: 800;
    color: var(--primary);
    margin: 1rem 0;
    position: relative;
    display: inline-block;
  }

  .score-value::after {
    content: '分';
    font-size: 1.5rem;
    margin-left: 0.25rem;
    color: #6b7280;
  }

  .score-bar {
    height: 8px;
    background: #e5e7eb;
    border-radius: 4px;
    overflow: hidden;
    margin: 1.5rem 0;
  }

  .progress {
    height: 100%;
    background: linear-gradient(90deg, var(--primary), #a5b4fc);
    transition: width 1s ease;
  }

  .score-tips {
    color: var(--success);
    font-weight: 500;
    font-size: 1.1rem;
  }

  @media (max-width: 640px) {
    .assignment-container {
      padding: 1rem;
    }

    .header h1 {
      font-size: 2rem;
    }

    .upload-section {
      padding: 1.5rem;
    }
  }
  </style>
