<template>
    <div class="viewport-container">
     <h2 class="title">🎵 节奏练习 🎵</h2>
      <div class="sheet-music" ref="sheetMusic">
        <!-- 第一行 -->
        <div class="line-container">
          <div class="line">
            <div class="time-signature">4/4</div>
            <div v-for="(symbol, index) in line1" :key="index"
                 class="symbol" :class="{ separator: ['|','||'].includes(symbol) }">
              {{ symbol }}
            </div>
          </div>
        </div>

        <!-- 第二行 -->
        <div class="line-container">
          <div class="line">
            <div class="time-signature">4/4</div>
            <div v-for="(symbol, index) in line2" :key="index"
                 class="symbol" :class="{ separator: ['|','||'].includes(symbol) }">
              {{ symbol }}
            </div>
          </div>
        </div>
      </div>
       <!-- 新增音频控制部分 -->
       <div class="audio-controls">
      <button class="play-button" @click="togglePlay">
        {{ isPlaying ? '暂停' : '播放' }}
      </button>

      <div class="speed-control">
        <button @click="adjustBpm(-1)">-1</button>
        <button @click="adjustBpm(-5)">-5</button>
        <input
          type="number"
          v-model.number="currentBpm"
          min="1"
          max="200"
          @change="validateBpm"
        >
        <button @click="adjustBpm(5)">+5</button>
        <button @click="adjustBpm(1)">+1</button>
        <span class="bpm-display">{{ currentBpm }} BPM</span>
      </div>
    </div>
    </div>
  </template>

<script>
export default {
  data () {
    return {
      line1: ['x̲x̲', 'x', 'x̲x̲', 'x', '|', 'x', '-', 'x', '-', '|', 'x̲x̲', 'x̲x̲', 'x', 'x', '|', 'x', '-', '-', '-', '||'],
      line2: ['x', 'x', 'x̲x̲', 'x', '|', 'x̲x̲', 'x', '-', 'x', '|', 'x̲x̲', 'x̲x̲', 'x', 'x', '|', 'x', '-', 'x', 'x', '||'],
      baseWidth: null, // 改为动态计算
      originalBpm: 120,
      currentBpm: 120,
      isPlaying: false,
      audioElement: null
    }
  },
  mounted () {
    this.calculateBaseWidth()
    this.updateScaling()
    window.addEventListener('resize', this.handleResize)
    this.audioElement = new Audio('jpq.mp3')
    this.updatePlaybackRate()

    this.audioElement.addEventListener('ended', () => {
      this.isPlaying = false
    })
  },
  methods: {
    calculateBaseWidth () {
      // 临时显示原始尺寸测量实际宽度
      this.$refs.sheetMusic.style.transform = 'scale(1)'
      this.$refs.sheetMusic.style.width = 'auto'
      this.baseWidth = this.$refs.sheetMusic.scrollWidth
    },
    handleResize () {
      this.updateScaling()
    },
    updateScaling () {
      const container = this.$refs.sheetMusic
      const parentWidth = container.parentElement.clientWidth

      // 动态计算缩放比例
      const scaleRatio = Math.min(
        parentWidth / this.baseWidth, // 宽度适配
        window.innerHeight * 0.8 / container.clientHeight // 高度适配
      )

      container.style.transform = `scale(${scaleRatio})`
      container.style.transformOrigin = '0 0'
      container.style.width = `${this.baseWidth}px`
    },
    togglePlay () {
      if (this.isPlaying) {
        this.audioElement.pause()
      } else {
        this.audioElement.play()
      }
      this.isPlaying = !this.isPlaying
    },

    adjustBpm (delta) {
      this.currentBpm = Math.min(Math.max(1, this.currentBpm + delta), 200)
      this.updatePlaybackRate()
    },

    validateBpm () {
      this.currentBpm = Math.min(Math.max(1, this.currentBpm), 200)
      this.updatePlaybackRate()
    },

    updatePlaybackRate () {
      // 计算播放速率：当前BPM / 原始BPM
      const rate = this.currentBpm / this.originalBpm
      this.audioElement.playbackRate = rate
    }
  },
  beforeDestroy () {
    if (this.audioElement) {
      this.audioElement.pause()
      this.audioElement.removeEventListener('ended')
    }
  }
}
</script>

  <style scoped>
  /* 标题样式 */
.title {
  font-size: 24px;
  font-weight: bold;
  color: #444;
  margin-bottom: 20px;
}
  /* 新增样式 */
/* 播放控制区域 */
.audio-controls {
  margin-top: -30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
}

/* 按钮通用样式 */
.play-button {
  padding: 12px 30px;
  font-size: 10px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s;
}

.play-button:hover {
  background-color: #218838;
}

/* BPM 调节部分 */
.speed-control {
  display: flex;
  align-items: center;
  background-color: #f8f9fa;
  padding: 10px;
  border-radius: 8px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
}
.speed-control input {
  width: 60px;
  text-align: center;
}
.speed-control button {
  padding: 6px 12px;
  border: 1px solid #ddd;
  background-color: white;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.2s;
}

.speed-control button:hover {
  background-color: #f0f0f0;
}
.bpm-display {
  margin-left: 10px;
  font-weight: bold;
  color: #2c3e50;
}
.speed-control span {
  min-width: 80px;
  text-align: center;
  font-size: 14px;
}
.viewport-container {
    width: 100%;
    overflow: hidden;
    padding: 20px 0;
  }

  .sheet-music {
    transform-origin: 0 0;
    transition: transform 0.3s ease;
  }

  .line-container {
    position: relative;
    margin: 15px 0;
  }

  .line {
    display: flex;
    align-items: center;
    white-space: nowrap;
    padding: 10px 0;
    position: relative;
  }

  .time-signature {
    margin-right: 20px;
    flex-shrink: 0;
    font-weight: 600;
    color: #444;
    font-size: 1.2em;
  }

  .symbol {
    flex-shrink: 0;
    margin: 0 8px;
    font-size: 28px;
    position: relative;
    transition: all 0.2s ease;
  }

  .symbol.separator {
    color: #666;
    font-weight: bold;
    margin: 0 12px;
  }

  /* 乐谱装饰线 */
  .line::after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: -8px;
    height: 2px;
    background: linear-gradient(90deg, transparent 2%, #ddd 50%, transparent 98%);
  }

  @media (hover: hover) {
    .symbol:hover {
      transform: scale(1.05);
      z-index: 1;
      cursor: pointer;
    }
  }
  </style>
