<template>
  <div> <et-tonghua></et-tonghua>
    <router-link
  to="/etmulu"
  class="magic-jump-button"
  active-class="active-portal"
>
  <span class="portal-icon">🚪</span>
  <span class="button-text">返回目录</span>
  <div class="portal-effects"></div>
</router-link></div>
</template>

<script>
import EtTonghua from '@/components/EtTonghua.vue'
export default {
  components: { EtTonghua }
}
</script>

<style>
.magic-jump-button {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 12px 24px;
  margin: 30px auto;
  border: none;
  border-radius: 25px;
  background: linear-gradient(135deg, #6e8efb 0%, #a777e3 100%);
  color: white !important;
  font-size: 1.2rem;
  font-weight: 600;
  text-decoration: none;
  text-shadow: 1px 1px 2px rgba(0,0,0,0.3);
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.25, 0.8, 0.25, 1);
  overflow: hidden;
  box-shadow: 0 4px 15px rgba(102, 51, 153, 0.4);
  transform: translateY(0);
}

.magic-jump-button:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(102, 51, 153, 0.6);
  background: linear-gradient(135deg, #7b9cff 0%, #b886f0 100%);
}

.magic-jump-button:active {
  transform: translateY(1px);
  box-shadow: 0 2px 10px rgba(102, 51, 153, 0.4);
}

.portal-icon {
  display: inline-block;
  margin-right: 12px;
  font-size: 1.5rem;
  transition: transform 0.3s ease;
}

.magic-jump-button:hover .portal-icon {
  animation: portal-spin 1s ease infinite;
}

@keyframes portal-spin {
  0% { transform: rotate(0deg) scale(1); }
  50% { transform: rotate(180deg) scale(1.2); }
  100% { transform: rotate(360deg) scale(1); }
}

.button-text {
  position: relative;
  z-index: 2;
}

/* 流光效果 */
.magic-jump-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 200%;
  height: 100%;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255,255,255,0.3),
    transparent
  );
  transition: 0.6s;
}

.magic-jump-button:hover::before {
  left: 100%;
}

/* 星光粒子效果 */
.portal-effects {
  position: absolute;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.portal-effects::before,
.portal-effects::after {
  content: '✨';
  position: absolute;
  opacity: 0;
  font-size: 12px;
  animation: sparkle 1.5s infinite;
}

@keyframes sparkle {
  0% { opacity: 0; transform: translateY(0); }
  20% { opacity: 1; }
  100% { opacity: 0; transform: translateY(-30px); }
}

.magic-jump-button:hover .portal-effects::before {
  left: 20%;
  animation-delay: 0.2s;
}

.magic-jump-button:hover .portal-effects::after {
  right: 20%;
  animation-delay: 0.5s;
}
</style>
