import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import kechengView from '@/views/kechengView.vue'
import AboutView from '@/views/AboutView.vue'
import OneView from '@/views/oneView.vue'
import TwoView from '@/views/twoView.vue'
import ThreeView from '@/views/threeView.vue'
import XinViewone from '@/views/xinViewone.vue'
import CreateViewone from '@/views/createViewone.vue'
import SingViewtwo from '@/views/singViewtwo.vue'
import XinViewTwo from '@/views/xinView.two.vue'
import CreateViewtwo from '@/views/createViewtwo.vue'
import SingViewthree from '@/views/singViewthree.vue'
