<template>
    <div class="image-container">
        <button
      class="audio-control"
      @click="toggleAudio"
      :class="{ 'is-playing': isAudioPlaying }"
    >
      {{ isAudioPlaying ? '🔊' : '🔇' }}
    </button>
      <img alt="sslx" src="sslx.png" class="enlarged-image">
    </div>
  </template>

<script>
export default {
  data () {
    return {
      // 新增音频相关数据
      isAudioPlaying: false,
      audioElement: null
    }
  },
  mounted () {
    // 初始化音频
    this.audioElement = new Audio('xc1.mp3') // 替换你的音频路径
    this.audioElement.loop = true // 开启循环播放
  },
  methods: {
    // 原有方法
    generateVideo () {
      this.showVideo = true
    },

    // 新增音频控制方法
    toggleAudio () {
      this.isAudioPlaying = !this.isAudioPlaying
      if (this.isAudioPlaying) {
        this.audioElement.play()
      } else {
        this.audioElement.pause()
      }
    }
  },
  beforeDestroy () {
    // 组件销毁时停止音频
    if (this.audioElement) {
      this.audioElement.pause()
      this.audioElement = null
    }
  }
}
</script>

  <style lang="less" scoped>
  .audio-control {
  position: fixed;
  top: 20px;
  right: 20px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.9);
  border: 2px solid #007bff;
  cursor: pointer;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  transition: all 0.3s ease;
  font-size: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.audio-control:hover {
  transform: scale(1.1);
  background: #007bff;
  color: white;
}

.audio-control.is-playing {
  background: #007bff;
  color: white;
}

  /* 让容器最大化 */
  .image-container {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
  }

  /* 让图片适应容器 */
  .enlarged-image {
    width: 95vw;  /* 让图片接近全屏 */
    height: auto; /* 保持原比例 */
    max-height: 95vh; /* 避免超出屏幕 */
    object-fit: contain;
    cursor: zoom-in;
    transition: transform 0.3s ease-in-out;
  }

  /* 鼠标悬停时放大 */
  .enlarged-image:hover {
    transform: scale(1.1);
  }
  </style>
