<template>
    <div class="detail-container">
      <h2>{{ provinceName }} 详细信息</h2>
      <button @click="$router.go(-1)">返回地图</button>
      <!-- 这里添加具体的省份信息内容 -->
      <div v-if="provinceData">
        <p>面积：{{ provinceData.area }} 平方公里</p>
        <p>人口：{{ provinceData.population }} 万</p>
        <!-- 更多信息... -->
      </div>
    </div>
  </template>

<script>
// 模拟省份数据
const mockData = {
  北京: {
    area: 16410,
    population: 2189
  },
  广东: {
    area: 179700,
    population: 12601
  }
  // 其他省份数据...
}

export default {
  props: ['name'],
  data () {
    return {
      provinceData: null
    }
  },
  computed: {
    provinceName () {
      return this.name || '未知省份'
    }
  },
  created () {
    this.fetchData()
  },
  methods: {
    fetchData () {
      // 实际项目中这里应该调用API
      this.provinceData = mockData[this.provinceName] || null
    }
  }
}
</script>

  <style scoped>
  .detail-container {
    padding: 20px;
    max-width: 800px;
    margin: 0 auto;
  }
  </style>
