<template>
    <div class="game-container">
        <div class="background">
      <div class="clouds"></div>
      <div class="mountains"></div>
      <div class="trees"></div>
    </div>
      <div class="police" v-if="!allCorrect">快帮它调整到44拍，让它开走！</div>
      <div class="police" v-else>🚓交警：可以出发啦！</div>

      <div class="train-track">
        <div class="train" :class="{ moving: allCorrect }">

            <div class="carriage engine">
          <div class="cowcatcher"></div>
          <div class="smokestack">
            <div class="smoke" v-for="i in 3" :key="i"></div>
          </div>
          <div class="headlight"></div>
          <div class="wheel-group">
            <div class="wheel" v-for="i in 4" :key="i"></div>
          </div>
        </div>
          <div
            v-for="(carriage, index) in carriages"
            :key="index"
            class="carriage"
            :class="{ correct: carriage.isCorrect }"
          >
              <!-- 添加车厢细节 -->
              <div class="roof"></div>
          <div class="window" v-for="i in 2" :key="i"></div>
          <div class="wheel-group">
            <div class="wheel" v-for="i in 4" :key="i"></div>
          </div>
            <div class="note"
                 v-for="(note, noteIndex) in carriage.notes"
                 :key="noteIndex"
                 @click="changeNote(index, noteIndex)"
            >
              {{ note.symbol }}
              <div class="duration">{{ note.nm }}</div>
            </div>
            <div class="time-signature">4/4</div>
          </div>
        </div>
      </div>

      <div class="controls">
      <button
        class="cartoon-btn"
        @click="checkAll"
        :class="{ dancing: allCorrect }"
      >
        <span class="sparkle">✨</span>
        检查所有车厢
        <span class="sparkle">🎵</span>
      </button>
    </div>

    </div>
  </template>

<script>
export default {
  data () {
    return {
      carriages: [
        {
          notes: [
            { symbol: '𝅝', duration: 4, nm: '全音符' },
            { symbol: '𝅗𝅥', duration: 2, nm: '二分音符' },
            { symbol: '𝅘𝅥𝅮', duration: 0.5, nm: '八分音符' },
            { symbol: '𝅘𝅥', duration: 1, nm: '四分音符' } // 正确
          ],
          isCorrect: false
        },
        {
          notes: [
            { symbol: '𝅗𝅥', duration: 2, nm: '二分音符' },
            { symbol: '𝅘𝅥𝅮', duration: 0.5, nm: '八分音符' },
            { symbol: '𝅝', duration: 4, nm: '全音符' },
            { symbol: '𝅘𝅥', duration: 1, nm: '四分音符' } // 正确
          ],
          isCorrect: false
        },
        {
          notes: [
            { symbol: '𝅘𝅥𝅮', duration: 0.5, nm: '八分音符' },
            { symbol: '𝅝', duration: 4, nm: '全音符' },
            { symbol: '𝅘𝅥', duration: 1, nm: '四分音符' }, // 正确
            { symbol: '𝅗𝅥', duration: 2, nm: '二分音符' }
          ],
          isCorrect: false
        },
        {
          notes: [
            { symbol: '𝅘𝅥', duration: 1, nm: '四分音符' }, // 正确
            { symbol: '𝅗𝅥', duration: 2, nm: '二分音符' },
            { symbol: '𝅘𝅥𝅮', duration: 0.5, nm: '八分音符' },
            { symbol: '𝅝', duration: 4, nm: '全音符' }
          ],
          isCorrect: false
        }
        // 可以添加更多车厢...
      ],
      trainState: 'waiting',
      noteOptions: [
        { symbol: '𝅝', duration: 4, nm: '全音符' }, // 全音符
        { symbol: '𝅗𝅥', duration: 2, nm: '二分音符' }, // 二分音符
        { symbol: '𝅘𝅥', duration: 1, nm: '四分音符' }, // 四分音符
        { symbol: '𝅘𝅥𝅮', duration: 0.5, nm: '八分音符' } // 八分音符
      ]
    }
  },
  computed: {
    allCorrect () {
      return this.carriages.every(c => c.isCorrect)
    }
  },
  methods: {
    handleButtonClick () {
      if (this.trainState === 'moving') {
        this.returnTrain()
      } else {
        this.checkAll()
      }
    },
    returnTrain () {
      this.trainState = 'returning'
      // 重置车厢状态（可选）

      // 1秒后重置火车位置
      setTimeout(() => {
        this.trainState = 'waiting'
      }, 1000)
    },
    checkCarriage (carriage) {
      const total = carriage.notes.reduce((sum, note) => sum + note.duration, 0)
      carriage.isCorrect = total === 4
    },
    changeNote (carriageIndex, noteIndex) {
      const current = this.carriages[carriageIndex].notes[noteIndex]
      const currentTypeIndex = this.noteOptions.findIndex(
        n => n.symbol === current.symbol
      )
      const nextTypeIndex = (currentTypeIndex + 1) % this.noteOptions.length
      this.$set(
        this.carriages[carriageIndex].notes,
        noteIndex,
        { ...this.noteOptions[nextTypeIndex] }
      )
      this.checkCarriage(this.carriages[carriageIndex])
    },
    checkAll () {
      this.carriages.forEach(this.checkCarriage)

      // 如果所有车厢正确，设置火车状态为移动
      if (this.allCorrect) {
        this.trainState = 'moving'
      } else {
        this.trainState = 'returning'

        // 2秒后重新设置为 "waiting"，让它重新出现
        setTimeout(() => {
          this.trainState = 'waiting'
        }, 1000)
      }
      location.reload()
    }

  }
}
</script>

  <style scoped>

  .game-container {
    padding: 20px;
    text-align: center;
    position: relative;
    overflow-x: auto; /* 允许左右滚动 */
    white-space: nowrap; /* 避免元素换行 */
  }
  .background {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.mountains {
  position: absolute;
  bottom: 30%;
  width: 150%;
  height: 200px;
  background: linear-gradient(
    to right,
    #6b8c9e 20%,
    #7fa4b8 50%,
    #6b8c9e 80%
  );
  clip-path: polygon(0 100%, 100% 100%, 100% 20%, 50% 0, 0 20%);
  filter: blur(8px);
  opacity: 0.6;
  animation: mountains-move 60s linear infinite;
}

  .train-track {
    width: 100%;
  height: 180px; /* 调整高度 */
  background: #666;
  position: relative;
  margin: 40px 0;
  overflow-x: auto; /* 允许横向滚动 */
  overflow-y: visible; /* 确保火车不会被垂直剪裁 */
}

.train {
  display: flex;
  position: absolute;
  left: 0; /* 初始位置居中 */
  transform:none;
  transition: all 5s cubic-bezier(0.4, 0, 0.2, 1);
}

.train.moving {
  left: -500%;
  transform: translateX(0);
}
@keyframes choo-choo {
  from { transform: translateX(0) rotate(-1deg); }
  to { transform: translateX(5px) rotate(1deg); }
}
.train.returning {
  left: 0 !important; /* 设置火车回到原点 */
  transform: scaleX(1); /* 恢复正常方向 */
  transition: all 3s ease-in-out;
}
.train.returning .wheel {
  animation-direction: reverse;
}
.train::after {
  content: "🚂";
  position: absolute;
  left: -40px;
  top: 20px;
  animation: smoke 2s infinite;
}
@keyframes smoke {
  0% { opacity: 0; transform: translateY(0); }
  50% { opacity: 1; content: "💨"; }
  100% { opacity: 0; transform: translateY(-20px); }
}

  .carriage {
    background: #fff;
    border: 3px solid #000;
    padding: 10px;
    display: flex;

  }
  .carriage.wagon {
  width: 160px;
  height: 80px;
  background: #FFF;
  border: 3px solid #444;
  position: relative;
  margin: 0 40px 0 20px;
}
  .carriage.correct {
    border-color: green;
  }
  .carriage.engine {
  width: 60px;
  height: 100px;
  background: #FF4444;
  border-radius: 15px 5px 5px 15px;
  position: relative;
  z-index: 2;
}
.roof {
  position: absolute;
  top: -15px;
  left: -5px;
  width: calc(100% + 10px);
  height: 30px;
  background: #FF4444;
  clip-path: polygon(0 0, 100% 0, 90% 100%, 10% 100%);
}

.cowcatcher {
  position: absolute;
  left: -30px;
  bottom: 0;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 40px 30px 0 0;
  border-color: #666 transparent transparent transparent;
}
.wheel-group {
  position: absolute;
  bottom: -20px;
  display: flex;
  gap: 10px;
}
.wheel {
  width: 25px;
  height: 25px;
  background: #444;
  border-radius: 50%;
  border: 3px solid #666;
  animation: wheel-rotate 2s linear infinite;
}
@keyframes wheel-rotate {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}
.smokestack {
  position: absolute;
  left: 80px;
  top: -30px;

  width: 20px;
  height: 40px;
  background: #444;
  border-radius: 3px;
}
.smoke {
  position: absolute;
  width: 20px;
  height: 20px;
  background: rgba(255,255,255,0.3);
  border-radius: 50%;
  animation: puff 3s infinite;
}
@keyframes puff {
  0% { transform: translateY(0) scale(0); opacity: 0; }
  50% { transform: translateY(-100px) scale(1.5); opacity: 0.3; }
  100% { transform: translateY(-200px) scale(2); opacity: 0; }
}
.headlight {
  position: absolute;
  right: 10px;
  top: 30px;
  width: 20px;
  height: 20px;
  background: #FFEE88;
  border-radius: 50%;
  box-shadow: 0 0 20px #FFEE88;
}
  .note {
    font-size: 40px;
    margin: 0 10px;
    cursor: pointer;
    transition: transform 0.2s;
  }

  .note:hover {
    transform: scale(1.1);
  }
  .note-container {
  position: absolute;
  top: 10px;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  gap: 10px;
}
  .duration {
    font-size: 12px;
    color: #666;
  }

  .time-signature {
    position: absolute;
    bottom: 5px;
    right: 5px;
    font-size: 12px;
  }

  .police {
    font-size: 20px;
    margin: 20px;
  }

  .controls {
    margin: 20px;
  }

  .cartoon-btn {
  position: relative;
  padding: 15px 30px;
  font-size: 20px;
  font-weight: bold;
  background: #FF6B6B;
  border: 4px solid #FFE66D;
  border-radius: 30px;
  color: white;
  text-shadow: 2px 2px #CC5454;
  box-shadow:
    0 8px 0 #CC5454,
    0 15px 20px rgba(0,0,0,0.3);
  cursor: pointer;
  transition:
    transform 0.1s,
    box-shadow 0.3s;
}

.cartoon-btn:hover {
  transform: translateY(-2px);
  box-shadow:
    0 10px 0 #CC5454,
    0 20px 25px rgba(0,0,0,0.4);
}

.cartoon-btn:active {
  transform: translateY(6px);
  box-shadow:
    0 2px 0 #CC5454,
    0 5px 10px rgba(0,0,0,0.3);
}

/* 按钮闪亮特效 */
.sparkle {
  display: inline-block;
  animation: sparkle-twinkle 1.5s infinite;
}

@keyframes sparkle-twinkle {
  0% { opacity: 0.5; transform: scale(1); }
  50% { opacity: 1; transform: scale(1.2); }
  100% { opacity: 0.5; transform: scale(1); }
}

/* 正确时的跳舞动画 */
.dancing {
  animation: btn-dance 0.8s ease infinite;
}

@keyframes btn-dance {
  0% { transform: rotate(0deg); }
  25% { transform: rotate(5deg); }
  50% { transform: rotate(-5deg); }
  75% { transform: rotate(3deg); }
  100% { transform: rotate(0deg); }
}
.cartoon-btn.returning {
  background: #4ECDC4;
  border-color: #45B7AF;
  box-shadow: 0 8px 0 #3AA79F;
}
/* 添加路由过渡动画 */
.magic-portal-enter-active,
.magic-portal-leave-active {
  transition: all 1s ease;
  position: absolute;
  width: 100%;
}

.magic-portal-enter-from {
  transform: translateX(100%) rotate(360deg);
  opacity: 0;
}

.magic-portal-leave-to {
  transform: translateX(-100%) rotate(-360deg);
  opacity: 0;
}

/* 调整原有按钮样式 */
.magic-jump-button {
  /* 保持原有样式 */
  /* 添加以下新属性 */
  transition: transform 0.5s cubic-bezier(0.68, -0.55, 0.27, 1.55);
}

.magic-jump-button:hover {
  transform:
    translateY(-5px)
    rotate(2deg)
    scale(1.05);
  filter: hue-rotate(45deg);
}

/* 添加传送门打开效果 */
.portal-effects::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(
    circle at var(--x) var(--y),
    rgba(255,255,255,0.3) 10%,
    transparent 30%
  );
  pointer-events: none;
}
  </style>
